@import url(https://fonts.googleapis.com/css2?family=KoHo&display=swap);
body {
  margin: 0;
  font-family: 'KoHo', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card_item{
    border:none !important;
}


.shadow_effect{
    box-shadow: 0;
    transition: box-shadow 1.9s, opacity 0.5s;
    will-change: transform;
}
.shadow_effect:hover{
    box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
}
.Unordered_list li{
    /* padding:8px 0; */
    transition: color 0.3s ;
}
.Unordered_list li:hover{
    color: #20C20E;
}
.heading_top{
    height:100vh;
}

#myVideo {
    width:100%;
    position:absolute;
    height:100%;
    left:50%;
    top:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
    object-fit:cover;
    z-index: -1;
  }
 

  .heading_top_container{
      padding-top:20%;
  }
  .Main_heading{
      font-weight: 900;
      font-size:110px;
      letter-spacing: 30px;
  }
  .Main_tagline{
      font-weight: 500;
      font-size:18px;
      letter-spacing:2px;
  }

  @media only screen and (max-width: 600px){

    .Main_heading{
        margin-top:50%;
        font-weight: 400;
        letter-spacing:5px;
        font-size:50px;
    }
    .main_tagline{
        font-weight:200;
        letter-spacing:1px;
        font-size:8px;
    }

  }
.shadow_effect{
    box-shadow: 0;
    transition: box-shadow 0.5s, opacity 0.5s;
    will-change: transform;
}
.shadow_effect:hover{
